body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding-top: 3.25rem;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header-wrapper {
  margin: 0 auto;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.movies-wrapper {
    margin-left: -15px;
}

ul {
    list-style-type: none;
}


.movies-item {
    min-height: 250px;
    box-shadow: 0.2rem 0.2rem 1rem #888888;
    padding: 15px;
    position: relative;
    overflow: hidden;
    line-height: 1.25;
    font-size: 12px;
    z-index: 0;
    margin: 0 15px 20px 0;
    min-width: 160px;
}

.movies-item header {
    position: absolute;
    z-index: 1;
    color: white;
    background: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    -webkit-transition:All 0.3s ease;
    transition:All 0.3s ease;
    height: 100%;
    overflow-y: auto;
    padding: 10px;
}

.movies-item:hover header {
    opacity: 1;
    height: 100%;
}

.hover-text {
    font-size: 13px;
    text-align: left;
    padding-bottom: 0;
    margin-bottom: 0;
}

.sub-text {
    font-size: 14px;
    font-weight: bold;
}

.movies-item img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}

.rating-details{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(255,255,255,0.8);
    padding: 5px;
    display: inline-block;
    text-align: center;
    font-size: 12px;
}

button {
    border: 1px solid #b0b0b0;
    border-radius: 4px;
    padding: 5px 5px;
    margin: 4px;
    font-size: 12.5px;
    -webkit-transition:All 0.3s ease;
    transition:All 0.3s ease;
}

.button-filter {
    text-align: right;
    border-right: 1px solid #b0b0b0;
}

.dropdown-filter {
    text-align: left;
}

button:hover{
    background: #1d171a;
    color: #fff;
}

.applied-filters{
    margin-top: 10px;
    padding-top: 10px;
    position: relative;
}

.genre {
    flex-grow: 1;
    padding: 0 3px;
    display: inline-block;
}

.applied-filters:before{
    position: absolute;
    content: '';
    width: 30%;
    height: 1px;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
}

.selected-genre {
    background: #da295a;
    color: #ffffff;
}

.applied-filters button {
    background: #da295a;
    color: #ffffff;
    font-weight: bold;
}

.applied-filters button:after{
    content: 'x';
    display: inline-block;
    padding-left: 2px;
    font-weight: bold;
}

.filter-wrapper {
    margin-top: 3rem;
    margin-bottom: 3rem;
}



